ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    margin-bottom: 30px;
}
input{
    margin-top: 15px;
    padding: 10px 15px;
    max-width: 150px;
    border-radius: 5px;
    margin-bottom: 15px;
}
label{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 18px;
}
.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(107, 183, 189);
    border: 1px solid black;
    border-radius: 4px;
    padding: 20px 30px;
}
.searchBtn{
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background-color: aqua;
    font-weight: 600;
    cursor: pointer;
}
.btn{
    padding: 15px 10px;
    border: none;
    border-radius: 10px;
    background-color: darkred;
    color: white;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}
.js-articles li {
    background: #fff;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 4px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.js-articles a {
    text-decoration: none;
    color: black;
    display: block;
}

.js-articles img {
    display: block;
    max-width: 100%;
    border-radius: 4px;
    margin-bottom: 10px;
}
.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
}